.blank {
  opacity: 0;
  height: 0px;
}

.countdown_wrapper {
  padding-bottom: 50px;
  padding-top: calc(12.5px + (25 - 12.5) * ((100vh - 434px) / (779 - 434)));
  min-height: 50vh;
}

.countdown_header {
  animation: slide_in_from_top .25s ease-in-out 1;
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
  border: 2px solid grey;
  border-radius: 50px 50px 0px 0px;
  box-shadow: 0 -10px 15px 0 rgba(0, 0, 0, 0.8), 0 0 15px 0 rgba(0, 0, 0, 0.8);
  margin: 0px auto 0 auto;
  padding: 0px 0px 0px 0px;
  text-align: center;
  width: calc(300px + (500 - 300) * ((100vw - 500px) / (1920 - 500)));
}

.countdown_header h3 {
  text-align: center;
  font-family: 'Audiowide';
  font-size: calc(30px + (55 - 30) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  padding: 10px;
  margin: 0px;
  color: white;
  text-align: center;
}

.countdown_timer_five {
  align-items: center;
  align-content: center;
  animation: slide_in_from_left .25s ease-in-out 1, shadow_blink 1.5s infinite;
  background-color: rgba(255,255,255, 1);
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
  border: 2px solid red;
  border-radius: 0px;
  box-shadow: 0 0 10px 0 rgba(255, 0, 0, 0.8), 0 10px 10px 0 rgba(255, 0, 0, 0.8);
  display: flex;
  color: white;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  margin: 0 auto 0 auto;
  padding: 0px;
  text-align: center;
  width: calc(300px + (500 - 300) * ((100vw - 500px) / (1920 - 500)));
}

.countdown_timer_five h1 {
  animation: text_shadow_blink_red 1.5s infinite;
  align-self: center;
  color: red;
  font-family: "Audiowide";
  font-size: calc(100px + (150 - 100) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
  text-shadow: 0 0 10px rgb(255, 0, 0);
  padding: 0px;
}

