.countdown_tutorial {
  animation: slide_in_from_right .25s ease-in-out 1;
  background-image: linear-gradient( rgb(50, 50, 50, 1) 10%,rgb(0, 0, 0, 1) 100%);
  border: 2px solid grey;
  border-radius: 0px 0px 50px 50px;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.8), 0 15px 15px 0 rgba(0, 0, 0, 0.8);
  display: flex;
  color: white;
  margin: 0 auto 10px auto;
  padding: 0px 10px 0px 10px;
  text-align: center;
  width: calc(280px + (480 - 280) * ((100vw - 500px) / (1920 - 500)));
}

.countdown_tutorial p, .dismount_countdown_tutorial p {
  width: 100%;
  padding: 10px;
  margin: 0 auto 0 auto;
  font-family: "Staatliches";
  font-size: calc(20px + (30 - 20) * ((100vw - 500px) / (1920 - 500)));
  font-weight: 300;
}